.home_wrap {
    max-width: 1280px;
    margin: 70px auto 0;
}

.home_wrap img {
    width: 100%;
    height: auto;
}

.top {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;

}

.top .banner1,
.top .banner4,
.top .banner7,
.top .banner10 {
    width: 40.5%;
}

.top .banner2,
.top .banner3,
.top .banner5,
.top .banner6,
.top .banner8,
.top .banner9,
.top .banner11,
.top .banner12 {
    width: 27%;
}

.top .text {
    width: 100%;
}

.top .text>strong {
    display: block;
    font-size: 1.4rem;
    font-weight: 700;
    margin: 30px 0 10px;
}

.top .text>p {
    line-height: 1.4em;
    font-size: 0.9rem;
}

.more {
    display: block;
    margin: 80px auto 30px;
    background: #fff url("./../../public/images/arrow.svg") no-repeat right 30px center;
    background-size: 5%;
    border: 1px solid #ccc;
    padding: 10px 100px;
    border-radius: 30px;
    cursor: pointer;
    font-size: 0.8rem;
}

@media screen and (max-width:1024px) {
    .home_wrap {
        max-width: 860px;
    }

    .top .text>strong {
        font-size: 1rem;
        margin-top: 15px;
    }

    .top .text>p {
        font-size: 0.7rem;
    }
}

@media screen and (max-width:840px) {

    .home_wrap {
        max-width: 650px;
    }

    .top {
        gap: 20px 0;
    }

    .top>div {
        width: 100%;
    }

    .top .imgbox {
        margin-top: 0px;
    }


}