.login_wrap {
    width: 400px;
    margin: 70px auto 0;
}
.login_wrap .title {
    padding-bottom: 20px;
    border-bottom: 4px solid #222;
    margin-bottom: 20px;
}
.login_wrap .title h2 {
    font-size: 2.6rem;
    font-weight: 500;
    text-align: center;
}

.login_wrap .login_area {
    margin-bottom: 30px;
}
.login_wrap input {
    width: 100%;
    height: 50px;
    padding-left: 10px;
    box-sizing: border-box;
    border: 1px solid #ccc;
}
.login_wrap .idbox {
    margin-bottom: 10px;
}
.login_wrap .pwbox {
    margin-bottom: 20px;
}
.login_wrap .login_btn {
    width: 100%;
    background: #222;
    color: #fff;
    height: 50px;
    font-size: 1.1rem;
    font-weight: 700;
    cursor: pointer;
}

.sns_login {
    margin-bottom: 70px;
}
.sns_login strong {
    display: block;
    font-size: 1rem;
    text-align: center;
    font-weight: 700;
    margin-bottom: 20px;
}
.sns_login > ul {
    width: 230px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
}
.sns_login > ul > li > a {
    display: block;
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background: url("./../../public/images/naver.png") no-repeat;
    background-size: cover;
    text-indent: -99999px;
}
.sns_login > ul > li > a.kakao {
    background: url("./../../public/images/kakao.png") no-repeat;
    background-size: cover;
}
.sns_login > ul > li > a.facebook{
    background: url("./../../public/images/facebook.png") no-repeat;
    background-size: cover;
}
.sns_login > ul > li > a.apple {
    background: url("./../../public/images/apple.png") no-repeat;
    background-size: cover;
}
 
.join {
    margin-bottom: 30px;
}
.join p {
    text-align: center;
    font-size: 0.8rem;
    font-weight: 500;
    margin-bottom: 15px;
}
.join p span {
    color: #ff421c;
}
.join .join_btn {
    width: 100%;
    height: 50px;
    border-radius: 28px;
    border: 1px solid #222;
    background: #fff;
    font-size: 0.9rem;
    font-weight: 700;
    color: #222;
    box-sizing: border-box;
}
.forget {
    margin-bottom: 80px;
}
.forget ul {
    display: flex;
    justify-content: space-between
}
.forget ul li:nth-child(1),
.forget ul li:nth-child(2){
    border-right: 1px solid #ededed;
    padding-right: 20px;
}
.forget ul li > a {
    font-size: 0.7rem;
    font-weight: 500;
}
.forget ul li:last-child > a {
    color: #666;
}