@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700&display=swap');
body, html {
    height: 100%;
}
body {
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 12px;
    background: #fff;
}

html {
    overflow-x: hidden;
}

a {
    color: #222;
}
#root {
    min-height: 100%;
    display: flex;
    flex-flow: column;
    
}
header {
    width: 100%;
    position: relative;
    z-index: 10;
    left: 0;
    top: 0;
    background: #fff;
}

.header_wrap {
    max-width: 1280px;
    margin: 30px auto 0;
    display: flex;
    justify-content: space-between;
}

.header_wrap .tnb ul {
    display: flex;
    gap: 20px;
}

.logo {
    width: 130px;
    height: 33px;
}

.logo .logo_bg {
    display: block;
    width: 100%;
    height: 100%;
    background: url('../public/images/logo.png') no-repeat;
    background-size: contain;
    text-indent: -99999px;
}

.mypage {
    padding-left: 22px;
    background: url('../public/images/mypage.svg') no-repeat left center;
    background-size: contain;
}

.mylike {
    padding-left: 22px;
    background: url('../public/images/mylike.svg') no-repeat left center;
    background-size: contain;
}

.cart {
    padding-left: 22px;
    background: url('../public/images/cart.svg') no-repeat left center;
    background-size: contain;
}

.login {
    padding-left: 22px;
    background: url('../public/images/login.svg') no-repeat left center;
    background-size: contain;
}

/* -- gnb -- */
.gnb {
    width: 100%;
    border-bottom: 1px solid #ccc;

    position: relative;
    z-index: 10;
}

.gnb_wrap {
    max-width: 1280px;
    margin: 30px auto 10px;
    display: flex;
}

.gnb_wrap>.gnb_menu {
    display: flex;
    flex-flow: row wrap;
    font-weight: 700;
    font-size: 1.2rem;
    border-right: 1px solid #ccc;
    position: relative;
}

.gnb_wrap>.gnb_menu>li {
    cursor: pointer;
    height: 30px;
    line-height: 30px;
    margin-right: 22px;
    padding: 0 4px;
    box-sizing: border-box;
}

.gnb_wrap>.gnb_menu>li.active {
    border-bottom: 2px solid #222;
}

.gnb_wrap>.gnb_menu>li .sub {
    width: 100vw;
    position: absolute;
    left: 0;
    top: 30px;
    display: none;
    padding-top: 20px;
    overflow-x: hidden;
    z-index: 5;
}

.gnb_wrap>.gnb_menu>li .sub ul {
    display: flex;
}

.gnb_wrap>.gnb_menu>li .sub li {
    font-size: 1.1rem;
    margin-right: 12px;
    font-weight: 500;
    padding: 0 4px;
}
.gnb_wrap>.gnb_menu>li .sub li.active {
    border-bottom: 2px solid #222;
}
.gnb_wrap>.gnb_menu>li .sub li:last-child {
    margin-right: 0;
}

.gnb_wrap>.board {
    display: flex;
    font-size: 1.2rem;
    margin-left: 26px;
}

.gnb_wrap>.board li {
    height: 30px;
    line-height: 30px;
    margin-right: 10px;
    font-weight: 300;
    cursor: pointer;
}
.sub_bg {
    width: 100vw;
    height: 55px;
    background: #fff;
    position: absolute;
    left: 0;
    top: 40px;
    display: none;
    border-bottom: 1px solid #ccc;
}
/* -- footer -- */

footer {
    margin-top: 30px;
    border-top: 1px solid #ccc;
    padding: 15px 0 10px;
    margin-top: auto;
}

footer .footer_wrap {
    max-width: 1280px;
    margin: 0 auto;
}

footer .footer_wrap .footer_menu {
    display: flex;
    gap: 10px;
    margin-bottom: 10px;
}

footer .footer_wrap .footer_menu li {
    font-size: 0.8rem;
    font-weight: 500
}

footer .footer_bottom {
    display: flex;
    justify-content: space-between;
}

footer .footer_wrap address p {
    margin-bottom: 3px;
    font-size: 0.6rem;
    font-weight: 500;
    color: #666;
}

footer .footer_wrap address p span {
    margin-right: 5px;
}

footer .kg {
    display: flex;
    align-items: center;
    gap: 15px;
}

footer .kg img {
    width: 30%;
    display: inline-block;
}

footer .kg p {
    display: inline;
    color: #777;
    font-size: 0.7rem;
}

.ready {
    padding: 30px 0;
    max-width: 1280px;
    margin: 70px auto 0;
    text-align: center;
    font-size: 3rem;
    font-weight: 700;
}