.pr_wrap {
    max-width: 1280px;
    margin: 40px auto;
    display: flex;
    justify-content: space-between;
    gap: 80px;
}
.title_wrap {
    margin-bottom: 20px;
}
.title_wrap h2 {
    width: 180px;
    font-size: 1.5rem;
    font-weight: 700;
    padding-bottom: 10px;
    border-bottom: 4px solid #222;
}
.pr_wrap .pr_menu {
    width: 180px;
    display: flex;
    flex-flow: column nowrap;
}
.pr_wrap .pr_menu .menu_list {
    padding-bottom: 20px;
}
.pr_wrap .pr_menu .menu_list li {
    font-size: 1rem;
    padding: 10px 0;
    cursor: pointer;
    font-weight: 500;
}

.pr_wrap .pr_menu .filter {
    border-top: 4px solid #222;
}
.pr_wrap .pr_menu .filter .title {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}
.pr_wrap .pr_menu .filter h3{
    font-size: 1.2rem;
    font-weight: 700;    
}
.pr_wrap .pr_menu .filter .init {
    background: transparent;
    cursor: pointer;
    font-size: 0.7rem;
}
.pr_wrap .pr_menu .filter .info {
    margin-top: 20px;
    padding-bottom: 30px;
}
.pr_wrap .pr_menu .filter .info strong {
    display: block;
    font-size: 1rem;
    font-weight: 500;
    margin-bottom: 20px;
}
.pr_wrap .pr_menu .filter .info li {
    margin-bottom: 10px
}
.pr_wrap .pr_menu .filter .info label {
    display: inline-block;
    font-size: 0.9rem;
}
.pr_wrap .pr_menu .filter .info input {
    vertical-align: -3px;
    margin-right: 10px;
    display: inline-block;
    width: 15px;
    height: 15px;
}
.pr_wrap .pr {
    max-width: 1000px;

}
.pr .category {
    width: 100%;
    background: #fff;
    border: 1px solid #ccc;
    box-sizing: border-box;
    margin-bottom: 30px;
    display: flex;
    flex-direction: row;
}
.pr .category > ul {
    display: inline-block;
    margin-left: 20px;
    display: flex;
    flex-flow: row wrap;
    
}
.pr .category > ul li {
    padding-right: 18px;
    margin-right: 18px;
    line-height: 50px;
    position: relative;
}
.pr .category > ul li::after {
    display: block;
    content: '';
    width: 1px;
    height: 15px;
    background: #ccc;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
}
.pr .category > ul li:last-child::after{
    display: none;
}
.pr .category > ul li > a {
    font-size: 0.8rem;
    color: #999;
    font-weight: 500;
}
.pr .category > ul li.active > a {
    color: #222;
} 
.pr .category > ul li:last-child {
    border-right: 0;
    padding-right: 20px
}

.pr .select {
    flex-grow: 1;
    position: relative;
    box-sizing: border-box;
    border-left: 1px solid #ccc;
}
.pr .select button {
    background: none;
    font-size: 0.8rem;
    cursor: pointer;
    width: 100%;
    text-align: center;
}
.pr .select .btn_box{
    background: url("../../public/images/arrow.svg")no-repeat right 15px center;
}
.pr .select .btn_select {
    height: 50px;
    line-height: 50px;
    
}
.pr .select .btn_list {
    display: none;
    position: absolute;
    left: -1px;
    top: 50px;
    width: 100%;    
    background: #fff;
    border: 1px solid #ccc;
    border-top: none;
    padding-bottom: 10px;
}
.pr .select .btn_list.on {
    display: block;
}
.pr .select .btn_list > li {
    margin-bottom: 15px
}
.pr .select .btn_list > li:last-child{
    margin-bottom: 0;
}



.pr .cloths {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    gap: 30px 0;
}

.pr .cloths > li {
    width: 32%

}
.pr .cloths > li img {
    width: 100%;
    height: auto;
}
.pr .cloths > li .pr_info {
    margin-top: 10px;
}
.pr .cloths > li .pr_info strong {
    display: block;
    font-size: 0.9rem;
    font-weight: 700;
    margin-bottom: 3px;
}
.pr .cloths > li .pr_info .p1 {
    font-size: 0.7rem;
    font-weight: 300;
    margin-bottom: 7px;
}
.pr .cloths > li .pr_info .p2 {
    font-size: 0.8rem;
    font-weight: 500;
    color: #999;
    margin-bottom: 7px;
}
.pr .cloths > li .pr_info .p3 {
    font-size: 0.8rem;
    color: #ff421c;
    font-weight: 700;
}
.pr .cloths > li .pr_info span {
    display: block;
    width: 50px;
    height: 20spx;
    text-align: center;
    line-height: 20px;
    border: 1px solid #e9e9e9;
    font-size: 0.6rem;
    margin-top: 10px;
}
@media screen and (max-width:1200px) {
    .pr_wrap {
        max-width: 860px;
    }
    .pr .cloths > li {
        width: 48%;
    }
}
@media screen and (max-width:840px) {
    .pr_wrap {
        max-width: 720px;
    }
    .pr .cloths > li {
        width: 100%
        
    }
}

